<template>
  <Scroll height style="text-align:left;" :on-reach-bottom="handleReachBottom">
    <BackTop></BackTop>
    <transition-group appear tag="ul">
      <li v-for="song in songlist" :key="song.idforres" style="list-style: none;">
        <div class="list-item">
          <span style="padding:6px">
            <Icon v-if="song.loved" size="24" type="md-heart" @click="hate(song)" />
            <Icon v-else size="24" type="md-heart-outline" @click="love(song)" />
          </span>
          <span
            class="limit-title"
            v-bind:class="{'disable': !song.playable}"
            @click="playSong(song)"
          >
            <p>
              <b>{{ song.name }}</b>
            </p>
            <p>{{ song.artist }}</p>
          </span>
          <span style="padding:6px;">
            <router-link style="color:black" :to="{ name: 'mvPlayer', params: { mv: song }}">
              <Icon v-if="song.mvid" custom="i-icon" size="24" type="ios-film-outline" />
            </router-link>
          </span>
        </div>
      </li>
    </transition-group>
  </Scroll>
</template>

<script>
import { loveSong, hateSong } from "@/api/apihub.js";

export default {
  name: "SongList",
  components: {},
  data() {
    return {
      EXCEED: false,
      page: 1,
      songlist: []
    };
  },
  methods: {
    hate(song) {
      hateSong({
        username: this.Global.username,
        songid: song.platform + song.idforres
      }).then(response => {
          if(response)
            this.$Message.success("成功从歌单移除");
          else
            this.$Message.error("移除失败");
      });
    },
    love(song) {
      if (this.Global.username) {
        loveSong({
          username: this.Global.username,
          songid: song.platform + song.idforres,
          info_str: JSON.stringify(song)
        }).then(response => {
          if(response)
            this.$Message.success("成功添加到歌单");
          else
            this.$Message.error("请勿重复添加");
        });
      } else {
        this.$Message.info("请先登录");
      }
    },
    playSong(song) {
      if (song.playable) {
        this.$emit("playmusic", song);
      } else {
        this.$Message["warning"]({
          background: true,
          content: "有版权限制哦"
        });
      }
    },
    add(list) {
      this.songlist = this.songlist.concat(list);
      if (list.length === 20) this.page += 1;
      else this.EXCEED = true;
    },
    set(list) {
      // this.$refs.container.scrollTop = 0;
      this.songlist = list;
      this.page = 1;
      this.EXCEED = list.length !== 20;
    },
    getSongs(){
      return this.songlist
    },
    handleReachBottom() {
      return new Promise(resolve => {
        if (this.EXCEED) this.$Message.info("没有更多歌曲了");
        else this.$emit("getnext", this.page, "songs");
        resolve()
      });
    }
  },
  created() {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.v-enter {
  opacity: 0;
  transform: translateY(80px);
}

.v-enter-active {
  transition: all 0.6s ease;
}

span {
  display: inline-block;
}

.list-item {
  padding: 10px 0;
  position: relative;
}

.list-item:active {
  background-color: rgb(179, 226, 245);
  cursor: pointer;
}

.disable {
  color: lightgray;
}

.limit-title {
  width: calc(100% - 75px);
  text-align: left;
  vertical-align: bottom;
}

.limit-title > p {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>
