import fetch from '@/utils/fetch'


export function search(platform, type, keyword, page) {
    return fetch({
        url: `/${platform}/${type}`,
        method: 'get',
        params: {
            keyword,
            page
        }
    })
}


export function getMV(platform, mvid) {
    return fetch({
        url: `/${platform}/uri/mv`,
        method: 'GET',
        params: {
            mvid
        }
    })
}

export function getComment(platform, idforcomments, type) {
    return fetch({
        url: `/${platform}/comments/${type}`,
        method: 'GET',
        params: {
            idforcomments
        }
    })
}

export function userLists(platform, user) {
    return fetch({
        url: `/${platform}/user/songlists`,
        method: 'get',
        params: {
            user
        }
    })
}

export function userLoved(username) {
    return fetch({
        url: '/user/loved/songs',
        method: 'get',
        params: {
            username
        }
    })
}

export function songsInList(platform, dissid, page) {
    return fetch({
        url: `/${platform}/songs/songlist`,
        method: 'get',
        params: {
            dissid,
            page
        }
    })
}

export function getSongUrl(platform, idforres) {
    return fetch({
        url: `/${platform}/uri/song`,
        method: 'GET',
        params: {
            idforres
        }
    })
}

export function extractAudio(params) {
    return fetch({
        url: '/extract',
        method: 'POST',
        data: params
    })
}

export function startdownload(params) {
    return fetch({
        url: '/download',
        method: 'POST',
        data: params
    })
}

export function downloadProgress(re_path) {
    return fetch({
        url: '/download/progress',
        method: 'GET',
        params: {
            re_path
        }
    })
}

export function findAudio(name) {
    return fetch({
        url: '/audio',
        method: 'GET',
        params: {
            name
        }
    })
}

export function signIn(params) {
    return fetch({
        url: '/login',
        method: 'POST',
        data: params
    })
}

export function signUp(params) {
    return fetch({
        url: '/sign',
        method: 'POST',
        data: params
    })
}

export function updateUser(params) {
    return fetch({
        url: '/user/info/update',
        method: 'POST',
        data: params
    })
}

/**
 * params = {
 *     username: this.loginsign.name,
 *     songid: song.platform + song.idforres,
 *     info_str: JSON.stringify(song),
 * }
 */
export function loveSong(params) {
    return fetch({
        url: '/user/love/song',
        method: 'POST',
        data: params
    })
}

/**
 * params = {
 *     username: this.loginsign.name,
 *     songid: song.platform + song.idforres
 * }
 */
export function hateSong(params) {
    return fetch({
        url: '/user/hate/song',
        method: 'POST',
        data: params
    })
}

export function getLyric(url) {
    return fetch({
        url,
        method: 'GET'
    })
}