<template>
  <div class="user">
    <Card style="margin:5px 0 10px 0" v-model="user">
      <div slot="title">
        <Avatar :src="user.profile" />
        <Button @click="showModal = true" type="text">{{login ? user.name : '未登录，点击登录'}}</Button>
        <Icon
          style="float:right; font-size: 20px; padding: 4px;"
          @click="showModal = true"
          type="md-person"
        />
      </div>
      <p @click="relaxionList()" v-if="respUser">
        <Icon type="md-heart-outline" size="16" />我的歌单
      </p>
    </Card>
    <Modal v-model="showModal" footer-hide :title="login? user.name : '登录 / 注册'">
      <div v-if="login" style="text-align:center">
        <Card>
          <img style="max-width: 140px;" :src="user.profile" />
          <Upload
            action="/upload/avator"
            :data="{user:respUser.name}"
            :on-success="uploadSucc"
          >
            <Button icon="ios-cloud-upload-outline">点击上传新头像</Button>
          </Upload>
        </Card>
        <Button @click="userQuit" type="warning" style="margin: 10px 0 0;">退出登录</Button>
      </div>
      <Form :label-width="60" v-else>
        <FormItem label="昵称">
          <Input placeholder="昵称" v-model="sign.username" />
        </FormItem>
        <FormItem label="密码">
          <Input placeholder="密码" v-model="sign.password" />
        </FormItem>
        <FormItem>
          <Button @click="Sign('in')" type="primary">登录</Button>
          <Button @click="Sign('up')" style="margin-left: 8px">注册</Button>
        </FormItem>
      </Form>
    </Modal>
    <Collapse simple>
      <Panel name="-1">
        个人账号绑定
        <Form slot="content" label-position="right" :label-width="90">
          <FormItem v-for="(pltf, key) in platforms" :key="key" :label="pltf.chName">
            <Input
              v-model="pltf.userAccount"
              :disabled="!pltf.ok"
              :placeholder="pltf.ok?'':'敬请期待'"
            />
          </FormItem>
          <FormItem>
            <Button type="primary" @click="bind()">一键绑定</Button>
          </FormItem>
        </Form>
      </Panel>
      <Panel v-for="(pltf, name) in platforms" :key="name" :name="name">
        {{pltf.chName}}歌单
        <Tag color="success" class="limited-tag" v-if="pltf.userAccount">{{pltf.userAccount}}</Tag>
        <p slot="content" v-if="!pltf.userAccount">还未绑定</p>
        <div slot="content" v-else>
          <div
            class="list-item"
            v-for="(item, index) in pltf.songLists"
            :key="index"
            @click="viewList(name, item.dissid)"
          >
            <div style="padding:0 4px;float:left">
              <img :src="item.pic" style="border-radius: 5px; height: 50px; width:50px;" />
            </div>
            <div class="limit-text">
              <b>{{ item.listname }}</b>
              <p>{{ item.num }}首</p>
            </div>
          </div>
        </div>
      </Panel>
    </Collapse>
    <transition name="slide-fade">
      <div class="myFrame" v-if="playlist.show">
        <div class="upFixedBar">
          <span @click="playlist.show = false">
            <Icon type="ios-arrow-back" />返回
          </span>
          <span style="float: right;" @click="playList">播放全部</span>
        </div>
        <div class="container">
          <SongList ref="Songs" @playmusic="playSong" @getnext="getNext" />
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import SongList from "@/components/SongList.vue";
import { userLists, songsInList, userLoved } from "@/api/apihub.js";
import { signIn, signUp, updateUser } from "@/api/apihub.js";

export default {
  name: "UserInfo",
  components: {
    SongList
  },
  data() {
    return {
      user: {
        name: "Kris",
        profile: "/resource/avators/default.jpg"
      },
      respUser: '',
      showModal: false,
      login: false,
      sign: {
        username: "",
        password: ""
      },
      LOCAL_STORAGE_KEY: "USER_INFO",
      platforms: {
        wangyi: {
          chName: "网易云音乐",
          userAccount: "",
          songLists: [],
          ok: true
        },
        qq: {
          chName: "QQ音乐",
          userAccount: "",
          songLists: [],
          ok: false
        },
        kugou: {
          chName: "酷狗音乐",
          userAccount: "",
          songLists: [],
          ok: false
        }
      },
      // song list
      playlist: {
        platform: "",
        id: "",
        show: false
      }
    };
  },
  watch: {
      respUser(user) {
        if (user) {
          this.Global.username = user.name;
        }
        else
          this.Global.username = '';
      }
  },
  methods: {
    Sign(type) {
      if (type == "in") {
        signIn(this.sign).then(response => {
          if (response["err"] == undefined) {
            // login success
            this.$Message.success("登录成功");
            this.showModal = false;

            localStorage.setItem(
              this.LOCAL_STORAGE_KEY,
              JSON.stringify(response)
            );
            this.freshUI(response);
          } else {
            this.$Message.info(response["err"]);
          }
        });
      } else {
        signUp(this.sign).then(response => {
          if (response["err"] == undefined) {
            this.$Message.success("注册成功，请登录");
          } else {
            this.$Message.info(response["err"]);
          }
        });
      }
    },
    userQuit() {
      localStorage.setItem(this.LOCAL_STORAGE_KEY, "");
      this.freshUI();
    },
    freshUI(user) {
      if (user) {
        this.respUser = user;

        this.user.name = user.name;
        let info = user.info;
        this.user.profile = info.avator ? info.avator : this.user.profile;

        this.login = true;

        for (const platform in this.platforms) {
          const data = this.platforms[platform];
          if (data.ok && info[platform] !== undefined) {
            this.platforms[platform].userAccount = info[platform];
            this.getSongLists(platform, info[platform]);
          }
        }
      } else {
        this.login = false;
        this.respUser = null;
        this.user.profile = "/resource/avators/default.jpg";

        Object.entries(this.platforms).forEach(keyvalue => {
          let platform = keyvalue[0];
          let data = keyvalue[1];
          if (data.ok) {
            this.platforms[platform].userAccount = "";
            this.platforms[platform].songLists = [];
          }
        });
      }
    },
    bind() {
      for (const platform in this.platforms) {
        let data = this.platforms[platform];
        if (data.userAccount) {
          this.respUser.info[platform] = data.userAccount;
        }
      }
      updateUser(this.respUser).then(response => {
        if (response["err"] == undefined) {
          this.$Message.success("绑定成功");

          localStorage.setItem(
            this.LOCAL_STORAGE_KEY,
            JSON.stringify(this.respUser)
          );

          this.freshUI(this.respUser);
        } else {
          this.$Message.info(response["err"]);
        }
      });
    },
    relaxionList() {
      this.playlist.show = true;
      this.playlist.platform = "relaxion";
      this.playlist.id = 0;

      userLoved(this.respUser.name).then(response => {
        this.$refs.Songs.set(response);
      });
    },
    viewList(platform, dissid) {
      this.playlist.show = true;
      this.playlist.platform = platform;
      this.playlist.id = dissid;

      songsInList(platform, dissid).then(response => {
        this.$refs.Songs.set(response.songs);
      });
    },
    uploadSucc(response) {
      let new_avator = response.url;

      this.$Message.success("成功更新头像");

      this.respUser.info["avator"] = new_avator;
      localStorage.setItem(
        this.LOCAL_STORAGE_KEY,
        JSON.stringify(this.respUser)
      );

      this.user.profile = new_avator;
    },
    playSong(song) {
      this.$emit("playmusic", song);
    },
    playList() {
      let list = this.$refs.Songs.getSongs();
      this.$emit("playlist", list);
    },
    getNext(page, _ = undefined) {
      console.log(_);
      songsInList(this.playlist.platform, this.playlist.id, page).then(list => {
        this.$refs.Songs.add(list.songs);
      });
    },
    getSongLists(platform, user) {
      userLists(platform, user).then(response => {
        response.lists.forEach(list => {
          this.platforms[platform].songLists.push({
            pic: list.pic,
            listname: list.name,
            num: list.songnum,
            dissid: list.dissid
          });
        });
      });
    }
  },
  created() {
    var cache = localStorage.getItem(this.LOCAL_STORAGE_KEY);
    if (cache) {
      this.freshUI(JSON.parse(cache));
    }
  }
};
</script>

<style scoped>
/* user info */

.user {
  text-align: left;
  overflow-y: auto;
  position: absolute;
  bottom: 0;
  top: 0;
  width: 100%;
}

.limited-tag {
  float: right;
  margin: 8px;
  max-width: calc(100% - 140px);
}

/* songlists */

.list-item {
  padding: 5px 0;
  position: relative;
}

.list-item:active {
  background-color: rgb(205, 241, 219);
  cursor: pointer;
}

.limit-text {
  padding: 6px 0 6px 6px;
  text-align: left;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/*
  songlist viewer
*/

.slide-fade-enter, .slide-fade-leave-to {
  transform: translateX(10px);
  transition: all .3s ease;
  opacity: 0;
}


.myFrame {
  position: fixed;
  top: 40px;
  width: 100%;
  bottom: 66px;
  background-color: white;
}
.upFixedBar {
  padding: 5px 10px;
  position: fixed;
  background-color: white;
  z-index: 2;
  width: 100%;
}

.container {
  overflow-y: auto;
  bottom: 66px;
  position: fixed;
  top: 68px;
  width: 100%;
}
</style>