<template>
  <div class="lyric">
    <ul id="lyric" @touchstart="touchstart" @touchend="touchend">
      <li v-if="!lyric" class="lyric-tip">没有歌词</li>
      <div style="height: 50%"></div>
      <li
        v-for="(text, time, index) in lyric"
        :key="index"
        :class="{'playing':index === currentLine}"
      >{{text}}</li>
      <div style="height: 50%"></div>
    </ul>
    <div class="process-line" v-if="adjust">
      <Icon class="border-button" type="ios-play" @click="playhere" />
    </div>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  name: "Lyric",
  props: {
    frontcolor: {
      default: "#a6fad1",
      type: String
    },
    backcolor: {
      default: "white",
      type: String
    },
    percent: {
      default: 0,
      type: Number
    }
  },
  data() {
    return {
      lyric: "",
      currentLine: 0,
      adjust: false,
      lasttimecount: null,
      lineHeights: []
    };
  },
  methods: {
    touchstart() {
      this.adjust = true;
      // 清除上一次倒计时
      if (this.lasttimecount) {
        clearTimeout(this.lasttimecount);
      }
    },
    touchend() {
      // 重新计时
      this.lasttimecount = setTimeout(() => {
        this.adjust = false;
      }, 1500);
    },
    playhere() {
      var height = document.getElementById("lyric").scrollTop;
      this.lineHeights.some((h, index) => {
        if (h > height) {
          let lyricline = Object.entries(this.lyric)[index - 1];
          this.$emit("jumptime", lyricline[0]);
          return true;
        }
      });
    },
    setLyric(str) {
      this.lyric = this.parseLyric(str); // 解析获取到的歌词
      this.$nextTick(this.countHeights);
    },
    countHeights() {
      let heights = [];
      let offset = 0;
      $("#lyric")
        .find("li")
        .each(function() {
          offset += $(this).innerHeight();
          heights.push(offset);
        });
      this.lineHeights = heights;
    },
    scrollLyric(time) {
      if (this.lyric === "" || this.adjust) return false;
      if (this.lineHeights[2] === 0) this.countHeights();
      var i = -1; // 获取当前歌词是在第几行
      for (var k in this.lyric) {
        if (k > time) break;
        i++;
      }
      // 还是这句
      if (i === this.currentLine) return;
      this.currentLine = i;

      var scroll = this.lineHeights[i];

      // get #lyric element
      $("#lyric")
        .stop()
        .animate({ scrollTop: scroll }, 600, "swing");
    },
    parseLyric(lrc) {
      // 解析歌词
      if (lrc === "") return "";
      var lyrics = lrc.split("\n");
      var lrcObj = {};
      for (var i = 0; i < lyrics.length; i++) {
        var lyric = decodeURIComponent(lyrics[i]);
        var timeReg = /\[\d*:\d*((\.|:)\d*)*\]/g;
        var timeRegExpArr = lyric.match(timeReg);
        if (!timeRegExpArr) continue;
        var clause = lyric.replace(timeReg, "");
        if (!clause) continue;
        for (var k = 0, h = timeRegExpArr.length; k < h; k++) {
          var t = timeRegExpArr[k];
          var min = Number(String(t.match(/\[\d*/i)).slice(1)),
            sec = Number(String(t.match(/:\d*/i)).slice(1));
          var time = min * 60 + sec;
          lrcObj[time] = clause;
        }
      }
      return lrcObj;
    }
  },
  created() {},
  mounted() {}
};
</script>
 

<style lang="scss">
