  height: 2px;
  color: black;
  background: -webkit-gradient(
    linear,
    right top,
    left top,
    from(#1a1a1a8f),
    color-stop(50%, #00ffff00),
    to(#1a1a1a8f)
  );
  background: linear-gradient(to left, #1a1a1a8f, #00ffff00 50%, #1a1a1a8f);
  position: absolute;
  top: 50%;
  width: 100%;
}

.border-button {
  float: right;
  font-size: 15px;
  padding: 0 4px;
  border-style: solid;
  border-radius: 5px;
  border-width: 1px;
  margin-top: 6px;
}

.icon-btn {
  position: absolute;
  width: 20px;
  height: 20px;
  top: -10px;
  right: -25px;
  font-size: smaller;
  border-radius: 50%;
  border-color: black;
  border-style: solid;
  border-width: 2px;
  line-height: 18px;
  z-index: 99;
}

/* 歌词显示区域 */
.lyric {
  position: relative;
  height: 100%;
  overflow: hidden;
  text-align: center;
  line-height: 28px;
}

/* 歌词 ul */
#lyric {
  position: absolute;
  width: 100%;
  top: 0;
  bottom: 0;
  overflow: scroll;
  mask-image: linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0) 0,
    rgba(255, 255, 255, 0.6) 15%,
    rgba(255, 255, 255, 1) 25%,
    rgba(255, 255, 255, 1) 75%,
    rgba(255, 255, 255, 0.6) 85%,
    rgba(255, 255, 255, 0) 100%
  );
  color: gray;
}

#lyric::-webkit-scrollbar {
  width: 0;
  height: 0;
}

#lyric > li {
  padding: 10px 0 0 0;
}

/* 正在播放的那一句歌词 */
#lyric .playing {
  color: black;
  font-weight: bold;
}

/* 歌词显示区显示的提示语(如加载中、无歌词等) */
.lyric-tip {
  position: absolute;
  width: 100%;
  top: 50%;
}
</style>
