<template>
  <div style="background-color: white; height: 100%;">
    <APlayer
      ref="aplayer"
      fixed
      listFolded
      disableVersionBadge
      :lrcType="3"
      :audio="list"
      @canplay="canplay"
      @pause="isPlay = false"
      @play="isPlay = true"
      @timeupdate="timeupdate"
    />
    <transition name="playpage">
      <Detail
        :currentTime="currentTime"
        :currentMusic="currentMusic"
        :isPlay="isPlay"
        @control="control"
        v-show="showDetail"
      />
    </transition>
    <div style="position: absolute;width: 100%;height: 66px;" @click="showPage"></div>
  </div>
</template>

<script>
import { APlayer } from "@moefe/vue-aplayer";
import Detail from "@/components/Player/Detail.vue";

export default {
  name: "Player",
  components: {
    APlayer,
    Detail
  },
  data() {
    return {
      list: [],
      showDetail: false,
      currentMusic: {},
      currentIndex: 0,
      currentTime: 0,
      isPlay: false,
      LOCAL_STORAGE_KEY: "playlist"
    };
  },
  methods: {
    control(cmd, params = undefined) {
      let player = this.$refs.aplayer;
      let commands = {
        skiplast: player.skipBack,
        toggle: player.toggle,
        pause: player.pause,
        skipnext: player.skipForward,
        hidedetail: () => {
          this.showDetail = false;
        },
        changetime: () => {
          player.seek(params);
        },
        switchsong: () => {
          player.switch(params);
        },
        remove: () => {
          player.skipForward();
          if (params === undefined) params = this.currentIndex;
          this.list.splice(params, 1);
        }
      };
      commands[cmd]();
    },
    notexist(song) {
      return (
        this.list.filter(
          s =>
            s["platform"] === song["platform"] &&
            s["idforres"] === song["idforres"]
        ).length === 0
      );
    },
    setMusic(song) {
      // check if song exists
      if (this.notexist(song)) {
        this.list.push(song);
        this.$refs.aplayer.switch(this.list.length - 1);
      } else this.$refs.aplayer.switch(song["name"]);
    },
    setList(list) {
      this.list = list;
    },
    showPage() {
      if (this.currentMusic.playable) {
        this.showDetail = true;
      }
    },
    storeCache() {
      localStorage.setItem(
        this.LOCAL_STORAGE_KEY,
        JSON.stringify({
          list: this.list
        })
      );
    },
    canplay() {
      // this.$refs.aplayer.play();
      this.currentMusic = this.$refs.aplayer.currentMusic;
      this.currentIndex = this.$refs.aplayer.currentIndex;
    },
    timeupdate() {
      this.currentTime = this.$refs.aplayer.media.currentTime;
    }
  },
  created() {
    var cache = JSON.parse(localStorage.getItem(this.LOCAL_STORAGE_KEY));
    if (cache) {
      this.list = cache.list;
    }
  },
  mounted() {
    this.currentMusic = this.$refs.aplayer.currentMusic;
    this.currentIndex = this.$refs.aplayer.currentIndex;
  },
  watch: {
    list: "storeCache",
  },
};
</script>

<style scope>
.fullpage {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  background: white;
  z-index: 100;
}

.aplayer-list li {
  text-align: left;
}
.aplayer-lrc {
  left: 84px !important;
}

.playlist-enter-active,
.playlist-leave-active {
  transition: opacity 0.5s;
}
.playlist-enter, .playlist-leave-to /* .playlist-leave-active below version 2.1.8 */ {
  opacity: 0;
}

.playpage-enter-active,
.playpage-leave-active {
  transition: all 0.3s ease;
}

.playpage-enter,
.playpage-leave-to {
  transform: translateY(100px);
  opacity: 0;
}
</style>