<template>
  <div id="app">
    <Layout style="height:100%">
      <div id="nav">
        <Icon
          v-if="notFull"
          type="md-expand"
          @click="fullscreen"
          custom="i-icon"
          size="16"
          style="position: fixed;left: 10px;top: 12px;"
        />
        <Icon
          v-else
          type="md-contract"
          @click="exitFullscreen"
          custom="i-icon"
          size="16"
          style="position: fixed;left: 10px;top: 12px;"
        />
        <span class="top-option">
          <router-link to="/">
            <Icon custom="i-icon" size="18" style="margin-bottom:2px" type="md-person" />用户
          </router-link>
        </span>
        <span class="top-option">
          <router-link to="/search">
            搜索
            <Icon custom="i-icon" size="18" style="margin-bottom:2px" type="md-search" />
          </router-link>
        </span>
      </div>
      <div class="content-container">
        <div class="content">
          <router-view @playmusic="playmusic" @playlist="playList" @shutup="pause" />
        </div>
      </div>
      <div class="footer">
        <Player ref="Player" />
      </div>
    </Layout>
  </div>
</template>
<script>
import Player from "@/components/Player.vue";

export default {
  name: "App",
  components: {
    Player
  },
  data() {
    return {
      notFull: true
    };
  },
  methods: {
    playmusic(song) {
      this.$refs.Player.setMusic(song);
    },
    playList(list) {
      this.$refs.Player.setList(list);
    },
    pause() {
      // play mv
      this.$refs.Player.control("pause");
    },
    fullscreen() {
      this.notFull = false;
      var docElm = document.documentElement;
      if (docElm.requestFullscreen) {
        docElm.requestFullscreen();
      } else if (docElm.mozRequestFullScreen) {
        docElm.mozRequestFullScreen();
      } else if (docElm.webkitRequestFullScreen) {
        docElm.webkitRequestFullScreen();
      } else if (docElm.msRequestFullscreen) {
        docElm.msRequestFullscreen();
      }
    },
    exitFullscreen() {
      this.notFull = true;
      if (document.exitFullscreen) {
        document.exitFullscreen();
      } else if (document.msExitFullscreen) {
        document.msExitFullscreen();
      } else if (document.mozCancelFullScreen) {
        document.mozCancelFullScreen();
      } else if (document.webkitExitFullscreen) {
        document.webkitExitFullscreen();
      }
    }
  },
  created() {}
};
</script>

