<template>
  <div class="fullfill stage">
    <div class="topBar">
      <Icon
        style="font-size: 25px; position: fixed; left: 10px;"
        @click="control('hidedetail')"
        type="ios-arrow-down"
      />
      <h3
        style="padding:0 40px; text-overflow:ellipsis; overflow:hidden; white-space:nowrap;"
      >{{currentMusic.name}}</h3>
    </div>
    <div class="myContent">
      <div>{{currentMusic.artist}}</div>
      <div class="albumHolder">
        <img :src="currentMusic.cover" style="width: 100%;height: 100%;" />
      </div>
      <div class="lyricArea">
        <Lyric ref="lyricArea" @jumptime="changeTime" />
      </div>
    </div>
    <div class="controlArea">
      <div style="height:20px; padding: 0 20px;">
        <div style="float: left; margin-top: -21px;">{{timeFormat(nowTime)}}</div>
        <div style="float: right; margin-top: -21px;">{{timeFormat(musicTime)}}</div>
        <Slider
          class="my-slider"
          :tip-format="timeFormat"
          v-model="nowTime"
          :max="musicTime"
          @on-input="Sliding"
          @on-change="changeTime"
        ></Slider>
      </div>
      <div class="buttons">
        <Icon
          style="font-size: 25px; margin-right: 10%;"
          type="ios-trash"
          @click="control('remove')"
        />
        <span class="circle-buttons">
          <Icon
            style="font-size: 20px; padding: 6px"
            type="md-skip-backward"
            @click="control('skiplast')"
          />
          <Icon
            style="font-size: 25px; padding: 10px"
            type="md-pause"
            v-if="isPlay"
            @click="control('toggle')"
          />
          <Icon
            style="font-size: 25px; padding: 10px"
            type="md-play"
            v-if="!isPlay"
            @click="control('toggle')"
          />
          <Icon
            style="font-size: 20px; padding: 6px"
            type="md-skip-forward"
            @click="control('skipnext')"
          />
        </span>
        <Icon
          style="font-size: 25px; margin-left: 10%;"
          @click="control('showlist')"
          type="ios-menu"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Lyric from "@/components/Player/Lyric.vue";
import { getLyric } from "@/api/apihub.js";

export default {
  name: "Detail",
  components: {
    Lyric
  },
  props: {
    currentMusic: Object,
    currentTime: Number,
    isPlay: Boolean
  },
  data() {
    return {
      nowTime: 0,
      musicTime: 0,
      jumpTime: 0
    };
  },
  methods: {
    timeFormat(v) {
      let min = '00' + parseInt(v / 60).toString();
      let sec = '00' + parseInt(v % 60).toString();
      return `${min.substr(-2,2)}:${sec.substr(-2,2)}`;
    },
    control(cmd, params = undefined) {
      this.$emit("control", cmd, params);
    },
    updateMusic() {
      this.jumpTime = 0;
      getLyric(this.currentMusic.lrc).then(response => {
        this.$refs.lyricArea.setLyric(response);
      });

      this.musicTime = this.currentMusic.interval;
    },
    timeupdate(old, time) {
      if (!this.jumpTime) {
        // sliding
        this.nowTime = time < this.musicTime ? time : this.nowTime;
        this.$refs.lyricArea.scrollLyric(parseInt(time));
      }
      if (time - this.jumpTime < 2 && time - this.jumpTime > -2) {
        this.jumpTime = 0;
      }
    },
    Sliding(val) {
      this.jumpTime = val;
      this.$refs.lyricArea.scrollLyric(parseInt(val));
    },
    changeTime(val) {
      // jumpTime 进度条调整的时候不会跳
      this.jumpTime = val;
      this.control("changetime", val);
    }
  },
  watch: {
    currentTime: "timeupdate",
    currentMusic: "updateMusic"
  },
  mounted() {}
};
</script>
 
